import Endpoint from '@/services/Endpoint'

export default {
  reportCategories (payload) {
    return Endpoint().get('/s1/report/reportCategory', {
      params: payload
    })
  },
  createReportCategory (payload) {
    return Endpoint().post('/s1/report/reportCategory', payload)
  },
  modifyReportCategory (payload) {
    return Endpoint().patch('/s1/report/reportCategory', payload)
  }
}
