import ReportCategoryService from '@/services/report/ReportCategoryService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ReportCategoryTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultReportCategoryTableFilter: {
      type: String,
      default: null
    },
    defaultReportCategoryTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      reportCategoryTableData: [],
      reportCategoryTableFilterTimer: null,
      reportCategoryTableFilter: this.defaultReportCategoryTableFilter,
      reportCategoryTableOrderBy: this.defaultReportCategoryTableOrderBy,
      reportCategoryTableContainer: 'loading-container',
      reportCategoryTableBusy: false,
      reportCategoryTableTotalRows: 0,
      reportCategoryTableCurrentPage: 1,
      reportCategoryTablePerPage: 10
    }
  },
  computed: {
    reportCategoryTableNoDataText: function () {
      return this.reportCategoryTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    reportCategoryTableTotalPages: function () {
      return Math.ceil(this.reportCategoryTableTotalRows / this.reportCategoryTablePerPage)
    },
    reportCategoryTablePagingInfo: function () {
      return this.pagingInfo(this.reportCategoryTableCurrentPage, this.reportCategoryTablePerPage, this.reportCategoryTableTotalRows)
    }
  },
  methods: {
    loadReportCategoryTableData (loadingContainerId) {
      let self = this
      this.reportCategoryTableBusy = true
      this.reportCategoryTableContainer = loadingContainerId || this.reportCategoryTableContainer
      this.startLoading(this.reportCategoryTableContainer)
      ReportCategoryService.reportCategories({
        pageIndex: this.reportCategoryTableCurrentPage - 1,
        pageSize: this.reportCategoryTablePerPage,
        filter: this.reportCategoryTableFilter,
        orderByField: this.reportCategoryTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.reportCategoryTableContainer)
        self.reportCategoryTableBusy = false
        self.reportCategoryTableTotalRows = response.data.totalRows
        self.reportCategoryTableData = response.data.reportCategoryList || []
      }).catch(function (error) {
        self.stopLoading(self.reportCategoryTableContainer)
        self.reportCategoryTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshReportCategoryTable () {
      this.loadReportCategoryTableData()
    },
    filterReportCategoryTable (filter) {
      let self = this
      if (this.reportCategoryTableFilter !== filter) {
        clearTimeout(this.reportCategoryTableFilterTimer)
        this.reportCategoryTableFilterTimer = setTimeout(() => {
          self.reportCategoryTableFilter = filter
          self.loadReportCategoryTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.reportCategoryTableFilterTimer)
      }
    },
    orderReportCategoryTable (orderBy, active) {
      this.reportCategoryTableOrderBy = (active ? '' : '-') + orderBy
      this.loadReportCategoryTableData()
    }
  },
  watch: {
    reportCategoryTableCurrentPage: function () {
      this.refreshReportCategoryTable()
    },
    reportCategoryTablePerPage: function () {
      this.refreshReportCategoryTable()
    }
  }
}

export default ReportCategoryTableMixin
